<template>
  <div class="choose-container">
    <div class="choose-bg">
      <img src="../assets/images/page_bg@2x.jpg" />
    </div>
    <div class="logo-img"><img src="../assets/images/logo@2x.png" /></div>
    <div class="camera-img"><img src="../assets/images/take_photo@2x.png" /></div>
    <div class="choose-button">
      <button @click="openAlbum(1)">上传照片</button>
      <button @click="openAlbum(2)">摄像头</button>
    </div>
  </div>
</template>

<script>
import {openCameraForPic} from '../common/wechat'
export default {
  name: 'ChoosePhoto',
  data () {
    return {
    }
  },
  methods: {
    openAlbum (type) {
      var that = this
      console.log('openAlbum')
      openCameraForPic(type, function (localIds) {
        console.log(localIds[0])
        window.localStorage.setItem('localIds', (localIds[0]))
        that.$router.push('/choosealbum')
      })
    }
  }
}
</script>

<style scoped lang="scss">
.choose-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  .choose-bg {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: -999;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .logo-img {
    position: absolute;
    top: 3rem;
    text-align: center;
    width: 100%;
    img {
      width: 150px;
    }
  }
  .camera-img {
    position: absolute;
    top: 180px;
    text-align: center;
    width: 100%;
    img {
      width: 150px;
    }
  }
  .choose-button {
    position: absolute;
    bottom: 100px;
    text-align: center;
    width: 100%;
    button {
      display: block;
      margin: auto;
      outline: none;
    }
    :first-child {
      margin-bottom: 20px;
    }
  }
}
</style>



